body, * {
  margin: 0;
  padding: 0; }

img {
  vertical-align: middle;
  max-width: 100%; }

.media {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
  align-items: flex-start; }

.media-body {
  -ms-flex: 1;
  flex: 1; }

.title {
  margin-bottom: 35px;
  h3 {
    color: #fff;
    margin: 0;
    position: relative;
    z-index: 1;
    font-weight: 300;
    font-size: 30px;
    display: inline-block;
    vertical-align: top;
    text-transform: uppercase;
    letter-spacing: 6px;
    &:before {
      content: "";
      position: absolute;
      bottom: -5px;
      left: 0;
      right: 0;
      width: 60px;
      background: #2fbf71;
      height: 1px; }
    &:after {
      content: "";
      width: 6px;
      height: 6px;
      border-radius: 50%;
      position: absolute;
      bottom: -7px;
      left: 56px;
      right: 0;
      z-index: -1;
      background: #2fbf71; } } }

.px-btn {
  padding: 0 25px;
  line-height: 42px;
  position: relative;
  display: inline-block;
  background: none;
  border: 1px solid transparent;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
  text-decoration: none !important;
  -moz-transition: ease all 0.35s;
  -o-transition: ease all 0.35s;
  -webkit-transition: ease all 0.35s;
  transition: ease all 0.35s;

  &.px-btn-theme {
    background: #2fbf71;
    color: #fff;
    box-shadow: 4px 4px #fff;
    &:hover {
      background: #fff;
      color: #2fbf71;
      box-shadow: 4px 4px #2fbf71; } } }

.separated {
  padding-top: 80px;
  padding-bottom: 80px;
  position: relative;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 1px;
    background: #fff;
    opacity: 0.1; } }

.header-left {
  width: 250px;
  position: fixed;
  top: 0;
  bottom: 0;
  background: #0c0c14;

  .header-scrollbar {
    height: calc(100% - 50px); }

  .header-top {
    text-align: center;
    position: relative;
    padding: 65px 25px 25px;
    &::after {
      content: "";
      position: absolute;
      top: -180px;
      left: -50px;
      right: 0;
      bottom: 0;
      z-index: -1;
      border-radius: 0 0 50% 50%;
      width: 310px;
      height: 310px;
      background: #2fbf71; } }

  .header-logo {
    width: 120px;
    height: 120px;
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    border-radius: 50%;
    border: 5px solid #fff; }

  .header-logo-name {
    margin: 15px 0 0;
    font-weight: 300;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 7px;
    color: #fff; }

  .social-icons {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 12px 0;
    a {
      color: #fff;
      font-size: 14px;
      margin-right: 17px;
      &:hover {
        color: #2fbf71; } } } }

.sidebar-menu {
  padding-top: 45px;
  padding-bottom: 45px;
  ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none; }

  li {
    width: 100%;
    .nav-link {
      display: block;
      margin: 0;
      padding: 15px 30px;
      color: rgba(255, 255, 255, 0.8);
      text-decoration: none;
      -moz-transition: ease all 0.35s;
      -o-transition: ease all 0.35s;
      -webkit-transition: ease all 0.35s;
      transition: ease all 0.35s;
      font-size: 12px;
      font-weight: 300;
      letter-spacing: 3px;
      text-transform: uppercase;
      line-height: 18px;
      i {
        font-size: 18px;
        font-weight: 300;
        display: inline-block;
        vertical-align: middle; }
      span {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px; }
      &:hover {
        color: #fff; } }
    &.active {
      &:hover {
        a {
          color: #2fbf71; } }
      a {
        color: #2fbf71;
        font-weight: 600; } }
    + li {
      border-top: 1px solid rgba(255, 255, 255, 0.03); } } }

.sidebar-right {
  left: 250px;
  position: absolute;
  height: 100%;
  width: calc(100% - 250px); }

