// portfolio page sass start  /////////////////////////////////////////////////////////////
.portfolio-filter-01 {
  padding-top: 15px;
  padding-bottom: 15px;
  .filter {
    li {
      cursor: pointer;
      margin: 0 15px;
      color: #fff;
      position: relative;
      padding: 5px 0;
      font-size: 13px;
      text-transform: uppercase;
      line-height: normal;
      letter-spacing: 2px;
      &:first-child {
        margin-left: 0; }
      &:last-child {
        margin-right: 0; }
      &.active:after {
        width: 100%; }
      &:after {
        content: "";
        width: 0px;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        height: 2px;
        background: #2fbf71;
        position: absolute;
        -moz-transition: ease all 0.55s;
        -o-transition: ease all 0.55s;
        -webkit-transition: ease all 0.55s;
        transition: ease all 0.55s; } } } }

.portfolio-content {
  column-count: 3;
  column-gap: 10px;
  &.grid-gutter-lg {
    margin-left: -12px;
    margin-right: -12px;
    .grid-item {
      padding: 12px; } }
  // &.grid-col-3
  //   .grid-item
  //     width: 33.3333%

  .grid-item {
    float: left;
    margin: 0;
    display: grid;
    grid-template-rows: 1fr auto;
    margin-bottom: 10px;
    break-inside: avoid; } }

.portfolio-box-01 {
